<template>
  <div class="claim-apply">
    <div class="claim-info">
      <div class="title-logo " v-if="groupOrderSn">
        <p class="title">{{groupOrder.plan_name}}</p>
        <p class="logo"><img :src="baseSrc+groupOrder.brandLog" alt="公司logo"></p>
      </div>
      <div class="title-logo " v-else>
        <p class="title">{{personalOrder.name}}</p>
        <p class="logo"><img :src="baseSrc+groupOrder.brand_logo" alt="公司logo"></p>
      </div>
      <div class="info-wrapper " v-if="groupOrderSn">
        <p class="info">保单号：{{groupOrder.policy_num}}</p>
        <p class="info">被保险人：{{userInfo.name}}</p>
        <p class="info">保障期间：{{groupOrder.policy_effective_date}}至{{groupOrder.policy_expiration_date}}</p>
      </div>
      <div class="info-wrapper" v-else>
        <p class="info">保单号：{{personalOrder.policy_num}}</p>
        <p class="info">被保险人：{{personalOrder.relation_name}}</p>
        <p class="info">保障期间：{{personalOrder.policy_effective_date}}至{{personalOrder.policy_expiration_date}}</p>
      </div>
    </div>
    <div class="claim-content">
      <p class="tips">请填写以下出险信息：</p>
      <form method="post" @submit.prevent="submit" class="claim-form">
        <div class="happen-time input-layer">
          <label>发生时间</label>
          <div class="input-wrapper" @click.prevent="open('timePicker')">
            <input type="text" v-model="claim.occurTime" placeholder="请选择" class="birthday-con" readonly>
            <i :style="dateShow?upImg:downImg"></i>
          </div>
          <mt-datetime-picker
            type="date"
            ref="timePicker"
            :closeOnClickModal="false"
            year-format="{value} 年"
            month-format="{value} 月"
            date-format="{value} 日"
            :startDate="new Date('2000-01-01')"
            :endDate="new Date(this.endDatefault)"
            @cancel="changeStartCancel"
            @confirm="handleChange">
            <!--:startDate="new Date('1900-01-01')"-->
          </mt-datetime-picker>
        </div>
        <div class="happen-place input-layer ">
          <label>发生地点</label>
          <div class=" input-wrapper" @click="popupAddr = true">
            <input v-model="claim.addr" placeholder="请选择" type="text"
                   readonly :disableClear=true>
            <i :style="popupAddr?upImg:downImg"></i>
          </div>
          <mt-popup v-model="popupAddr" position="bottom" class="mint-popup-4">
            <div class="page-picker-wrapper">
              <div class="confirm-btn">
                <p class="btn cancel" @click.prevent="addrCancel">取消</p>
                <p class="btn sure" @click.prevent="addrSelect">确定</p>
              </div>
              <mt-picker :slots="addressSlots" @change="onAddressChange" :visible-item-count="5"></mt-picker>
            </div>
          </mt-popup>
        </div>
        <div class="addr-detail input-layer ">
          <label>详细街道</label>
          <div class="input-wrapper">
            <!--<div contenteditable class="textarea needsclick" :class="isEdit?'edit':''" @focus="getFocus" @blur="getBlur"-->
                 <!--style="-webkit-user-select:text" @input="changeVal"><i class="addr-detail-text">{{detailArrText}}</i></div>-->
        <!--    <textarea placeholder="请输入" v-model="claim.addrDetail"></textarea>-->
            <textarea name=""  v-model="claim.addrDetail" cols="" rows="1" class="input-item" placeholder="请输入"  ></textarea>
            <!--<input v-model="claim.addrDetail"  type="text" placeholder="请输入" style="padding-right:.1rem"></input>-->
            <!--<i class="close" v-show="claim.addrDetail&&focusShow" @mousedown.stop="claim.addrDetail=''"></i>-->
          </div>
        </div>
        <div class="happen-profit ">
          <label>保障利益</label>
          <ul>
            <li v-for="(profit,index) in profits" :key="index">
              <div class="profit-con">
                <input class="self-radio" type="radio"
                       :id="'radio-'+profit.id"
                       name="radio"
                       :value="profit.id"
                       v-model="select_profit"
                       @click="setCheckRadioValue(profit)">
                <label :for="'radio-'+profit.id"></label>
                <span>{{profit.rights}}</span>
              </div>
              <i @click="showProfitDetail(index)" :id="'profit-detail-i'+index" v-show="profit.parameter_value"></i>
              <p class="profit-detail" :id="'profit-detail'+index" ref='profitDetail'
                 v-show="currentProfitIndex===index">
                {{profit.parameter_value}} <i
                class="arrow"></i><em></em></p>
            </li>
          </ul>
        </div>
        <div class="happen-des ">
          <label>事件描述 <span>（400字以内）</span></label>
          <textarea placeholder="请输入起因经过结果，如果为治疗，请写明医院及科室等信息" v-model="claim.detail" maxlength=400 ></textarea>
        </div>
        <div class="btnNext-wrapper">
          <!--<input type="submit" class="btnNext" :disabled="disShow" value="下一步" :class="allFill? 'active':''">-->
          <input type="submit" class="btnNext" :disabled="!allFill" value="下一步" :class="allFill? 'active':''">
        </div>
      </form>
    </div>
    <m-prompt
      v-show="calculate"
      @handleBtnLeft="handleCancel"
      @handleBtnRight="hanldeCalcute"
      :btnLeftText="'取消'"
      :btnRightText="'确认授权'"
      :content="'同意并授权XX保险公司及其授权的机构和个人以您的名义向医疗机构、公安部门、保险公司、其他组织机构及个人索取、查询、调阅、摘抄、复印/制所有理赔相关材料，并具有同等效力，您愿承担由此产生的一切法律后果。'">
      <div class="promptInnerPadding"></div>
    </m-prompt>
  </div>
</template>

<script>
import mPrompt from '@/components/base/mPrompt'
import myaddress from '../../..//public/pca.json'
import moment from 'moment'
import { mapGetters } from 'vuex'

export default {
  name: 'claimApply',
  data () {
    return {
      calculate: false,
      isEdit: false,
      detailArrText: '请输入',
      baseSrc: '',
      endDatefault: moment(new Date()).format('YYYY-MM-DD'),
      orderSn: '',
      currentProfitIndex: '',
      selProfitWay: '', // 0：在线提交，1：柜台提交，2：在线理算，3：医疗票据在线理算
      groupOrderSn: '', // 团险订单编号
      sn: '',
      groupOrder: {},
      personalOrder: {},
      profits: [],
      select_profit: '',
      disShow: true,
      addressProvince: '省',
      addressCity: '市',
      addresscounty: '区/县',
      addressSlots: [
        {
          flex: 1,
          defaultIndex: 1,
          values: Object.keys(myaddress), // 省份数组
          className: 'slot1',
          textAlign: 'center'
        }, {
          divider: true,
          content: '-',
          className: 'slot2'
        }, {
          flex: 1,
          values: [],
          className: 'slot3',
          textAlign: 'center'
        },
        {
          divider: true,
          content: '-',
          className: 'slot4'
        },
        {
          flex: 1,
          values: [],
          className: 'slot5',
          textAlign: 'center'
        }
      ],
      upImg: {
        'background-image':
          'url(' + require('@/assets/images/icon_up.png') + ')'
      },
      downImg: {
        'background-image':
          'url(' + require('@/assets/images/icon_down.png') + ')'
      },
      popupAddr: false,
      dateShow: false,

      claim: {
        occurTime: '',
        addr: '',
        addrDetail: '',
        profit: '',
        detail: ''
      }
    }
  },
  mounted () {
    this.$nextTick(() => { // vue里面全部加载好了再执行的函数  （类似于setTimeout）
      this.addressSlots[0].defaultIndex = 0
    })
    this.baseSrc = `${process.env.VUE_APP_RESOURCE_URL}`
    if (this.$route.query.groupOrderSn) {
      this.groupOrderSn = this.$route.query.groupOrderSn
    }
    if (this.$route.query.type == 0) {
      // 个险
      this.orderSn = this.$route.query.orderSn
    }
    if (this.$route.query.type && this.$route.query.type == 1) {
      // 团险
      this.groupOrderSn = this.$route.query.orderSn
    }
    this.getClaimApply()
    document.addEventListener('click', (e) => {
      for (let i = 0; i < document.getElementsByClassName('profit-detail').length; i++) {
        let box = document.getElementById('profit-detail' + i)
        let box1 = document.getElementById('profit-detail-i' + i)
        if (this.currentProfitIndex === i && !box.contains(e.target) && !box1.contains(e.target)) {
          this.currentProfitIndex = ''
        }
      }
    })
  },
  methods: {
    saveMaterial () {
      let orderSn = ''
      if (this.groupOrderSn) {
        orderSn = this.groupOrderSn
      } else {
        orderSn = this.orderSn
      }
      let postData = {
        order_sn: orderSn,
        occur_time: this.claim.occurTime,
        area_name: this.claim.addr,
        address: this.claim.detail,
        detail: this.claim.addrDetail,
        claim_calculation_id: this.claim.profit,
        sn: this.sn
      }
      this.$api.claim.claimApplySubmit(postData).then(res => {
        if (res.code === 1) {
          if (this.applyWay !== 'calculate') {
            this.$router.push({
              path: '/claimUpload',
              query: {
                claimApplySn: res.sn
              }
            })
          } else {
            console.log('calcuteSucceed')
          }
        } else {
          // this.disShow = false
          this.$toast.center(res.message)
        }
      })
    },
    submit () {
      if (this.applyWay === 'calculate') {
        this.calculate = true
      } else {
        this.saveMaterial()
      }
    },
    hanldeCalcute () {
      this.saveMaterial()
    },
    handleCancel () {
      this.calculate = false
    },
    showProfitDetail (i) {
      this.currentProfitIndex = i
    },
    getClaimApply () {
      let postData = {
        groupOrderSn: this.groupOrderSn,
        orderSn: this.orderSn,
        sn: ''
      }
      this.$api.claim.getClaimApply(postData).then(res => {
        if (res.code === 1) {
          this.profits = res.claim
          if (this.groupOrderSn) {
            this.groupOrder = res.groupInsuranceOrder
          } else {
            this.personalOrder = res.order
          }
        } else {
          this.$toast.center(res.message)
        }
        if (res.isEdit === 1) {
          this.isEdit = true
          this.claim.occurTime = moment(res.claimRecord.occur_time).format('YYYY-MM-DD')
          this.claim.addr = res.claimRecord.area_name
          this.claim.addrDetail = res.claimRecord.address
          this.detailArrText = res.claimRecord.address
          this.claim.profit = res.claimRecord.claim_calculation_id
          this.claim.detail = res.claimRecord.detail
          this.select_profit = res.claimRecord.claim_calculation_id
          this.selProfitWay = res.claimRecord.status
        }
      })
    },

    setCheckRadioValue (item) {
      this.select_profit = item.id
      this.claim.profit = item.id
      this.selProfitWay = item.status
    },
    onAddressChange (picker, values) {
      if (myaddress[values[0]]) {
        picker.setSlotValues(1, Object.keys(myaddress[values[0]]))
        picker.setSlotValues(2, myaddress[values[0]][values[1]])
        this.addressProvince = values[0]
        this.addressCity = values[1]
        this.addresscounty = values[2]
      }
    },
    addrSelect () {
      this.claim.addr = `${this.addressProvince}-${this.addressCity}-${this.addresscounty}`
      this.popupAddr = false
    },
    addrCancel () {
      this.popupAddr = false
    },
    // 日期
    open (picker) {
      this.$refs[picker].open()
      this.dateShow = true
      // this.birthdayShow = true;
      // this.insured.birthday = this.formatDate(value.toString());
    },

    handleChange (value) {
      this.dateShow = false
      this.claim.occurTime = moment(value).format('YYYY-MM-DD')
      // this.claim.occurTime = this.formatDate(value.toString())
    },
    changeStartCancel () {
      this.dateShow = false
    },
    // textarea变形
    getFocus (val) {
      console.log(123)
      if (!this.claim.addrDetail) {
        console.log(321)
        val.target.innerText = ''
        this.claim.addrDetail = ''
        val.target.style.color = '#1C1D1D'
        // this.placeholderText = '';
      }
    },
    getBlur (val) {
      if (val.target.innerText === '') {
        val.target.innerText = '请输入'
        val.target.style.color = '#D5D8D8'
        this.claim.addrDetail = ''
      }
    },
    changeVal (val) {
      console.log(456)
      this.claim.addrDetail = val.target.innerText
    }
  },
  computed: {
    allFill () {
      let _status = this.claim.occurTime && this.claim.addr && this.claim.addrDetail && this.claim.profit && this.claim.detail
      // if (_status === '' || _status === null) {
      //   this.disShow = true
      // } else {
      //   this.disShow = false
      // }
      return _status
    },
    applyWay () {
      switch (this.selProfitWay) {
        case 0:
          return 'online'
        case 1:
          return 'counter'
        case 2:
          return 'calculate'
        case 3:
          return 'medical'
      }
    },
    ...mapGetters(['userInfo'])
  },
  components: {
    mPrompt
  }
}
</script>

<style lang="scss">
  @import "@/assets/styles/mixin.scss";
  .claim-apply {
    position: fixed;
    z-index: 100;
    top: 0.84rem;
    left: 0;
    bottom: 0;
    right: 0;
    background: #F5F5F5;
    z-index: 200;
    overflow-y: scroll;
    .claim-info {
      background: #fff;
      margin-bottom: pxToRem(20px);
      .title-logo {
        padding-left: pxToRem(30px);
        display: flex;
        justify-content: space-between;
        padding: pxToRem(26px) 0 0 pxToRem(30px);
        .title {
          font-size: pxToRem(36px);
          color: $color-text;
          font-weight: bold;
          line-height: pxToRem(46px);
          width: 5.7rem;
        }
        .logo {
          position: absolute;
          right: pxToRem(30px);
          top: pxToRem(26px);
          width: pxToRem(110px);
          height: pxToRem(60px);
          img {
            display: block;
            width: pxToRem(110px);
            height: pxToRem(60px);
          }
        }
      }
      .info-wrapper {
        position: relative;
        padding: pxToRem(16px) pxToRem(42px) pxToRem(17px) pxToRem(36px);
        .info {
          font-size: pxToRem(26px);
          color: $color-text;
          line-height: pxToRem(42px);
          display: flex;
          justify-content: space-between;
          width: 100%;
        }
      }
    }
    .claim-content {
      background: #fff;

      .tips {
        font-size: pxToRem(30px);
        color: #B6BBBB;
        padding: pxToRem(30px) 0 pxToRem(5px) pxToRem(30px);
      }
      .claim-form {
        .input-layer {
          margin-left: pxToRem(30px);
          padding-right: pxToRem(20px);
          font-size: pxToRem(30px);
          display: flex;
          justify-content: space-between;
          border-bottom: $border-bottom-light-color;
          label {
            width: pxToRem(126px);
            padding: pxToRem(35px) 0;
          }
          .input-wrapper {
            position: relative;
            flex: 1;
            display: flex;
            align-items: center;
            justify-content: space-between;
            input {
              flex: 1;
              height: 100%;
              /*height: pxToRem(100px);*/
              /*line-height: pxToRem(100px);*/
              text-align: right;

            }
            .textarea {
              border: none;
              outline: none;
              flex: 1;
              text-align: right;
              font-size: pxToRem(30px);
              line-height: pxToRem(48px);
              padding: pxToRem(26px) 0 pxToRem(26px) pxToRem(30px);
              color: #D5D8D8;
              padding-right: pxToRem(30px);
              &.edit {
                color: $color-text;
              }
            }
            i {
              display: block;
              width: pxToRem(44px);
              height: pxToRem(44px);
              background-size: cover;
            }
          }

        }
        .happen-profit {
          margin-left:pxToRem(20px);
          label {
            height: pxToRem(100px);
            line-height: pxToRem(100px)
          }
          ul {
            border-bottom: $border-bottom-light-color;
            padding-right: pxToRem(25px);
            li {
              position: relative;
              margin-bottom: pxToRem(30px);
              display: flex;
              justify-content: space-between;
              align-items: center;
              .profit-con {
                flex: 1;
                display: flex;
                align-items: center;
              }
              span {
                margin-left: pxToRem(26px);
                display: inline-block;
              }
              i {
                background: url("../../assets/images/icon_explain.png");
                background-size: pxToRem(44px) pxToRem(44px);
                width: pxToRem(44px);
                height: pxToRem(44px);
              }
              .profit-detail {
                position: absolute;
                z-index: 1;
                top: pxToRem(66px);
                left: pxToRem(56px);
                background: #FFFFFF;
                border: $border-bottom-light-color;
                box-shadow: 0 pxToRem(6px) pxToRem(12px) 0 rgba(182, 187, 187, 0.45);
                border-radius: pxToRem(4px);
                width: pxToRem(568px);
                /*height: pxToRem(146px);*/
                font-size: pxToRem(26px);
                line-height: pxToRem(42px);;
                color: #A5A8A8;
                padding: pxToRem(36px) pxToRem(30px);
                .arrow {
                  position: absolute;
                  top: pxToRem(-34px);
                  left: pxToRem(42px);
                  width: 0;
                  height: 0;
                  font-size: 0;
                  border: pxToRem(16px) solid #EDEDED;;
                  border-color: #fff #fff #EDEDED #fff;
                }
                em {
                  position: absolute;
                  top: pxToRem(-30px);
                  left: pxToRem(42px);
                  border: pxToRem(16px) solid #EDEDED;;
                  border-color: transparent transparent #FFF;
                }
              }
              .self-radio {
                display: none;
              }
              .self-radio + label {
                -webkit-appearance: none;
                background-color: #fff;
                border: pxToRem(2px) solid #C8CBCB;
                border-radius: 1px;
                display: inline-block;
                position: relative;
                width: pxToRem(35px);
                height: pxToRem(35px);
                border-radius: 50%;
                box-sizing: border-box;
                vertical-align: top;
              }
              .self-radio:checked + label:after {
                display: inline-block;
                text-align: center;
                content: '';
                width: pxToRem(16px);
                height: pxToRem(16px);
                border-radius: 50%;
                position: absolute;
                top: pxToRem(8px);
                left: pxToRem(8px);
                background: $color-active-text;
              }
            }
          }
        }
        .happen-des {
          margin-left:pxToRem(20px);
          padding-right: pxToRem(30px);
          label {
            height: pxToRem(100px);
            line-height: pxToRem(100px);
            display: block;
            span {
              font-size: pxToRem(26px);
              color: #D5D8D8;
            }
          }
          textarea {
            border: $border-bottom-light-color;
            border-radius: pxToRem(4px);
            padding: pxToRem(20px) pxToRem(30px);
            font-size: pxToRem(30px);
            color: $color-text;
            line-height: pxToRem(48px);
            width: 100%;
            box-sizing: border-box;
            height: pxToRem(400px);
            margin-bottom: pxToRem(30px);
            &:focus {
              outline: none;
            }
          }
        }
        .addr-detail{
          textarea{
            padding-right:pxToRem(15px);
            padding-left:pxToRem(10px);
            width: 100%;
            outline: none;
            border: none;
            text-align: right;
            line-height: pxToRem(48px);
            overflow-y:visible;
          }

        }
        .btnNext-wrapper {
          padding-bottom: pxToRem(37px);
          background: #f5f5f5;
          padding-top: pxToRem(50px);
          input {
            @include longBtn;
            opacity: 0.45;
            box-shadow: none;
            &.active {
              opacity: 1;
            }
          }

        }
      }

    }
  }

  .promptInnerPadding {
    margin-top: pxToRem(-20px);
  }

  .confirm-btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    .btn {
      width: 50%;
    }

  }

  .mint-popup {
    width: 100%;
    font-size: pxToRem(30px);
  }

  .picker-toolbar, .confirm-btn {
    height: pxToRem(100px);
    line-height: pxToRem(100px);
    border-bottom: $border-bottom-light-color;
    font-size: pxToRem(34px);
  }

  .mint-datetime-confirm, .confirm-btn .sure {
    color: $color-active-text;
    height: pxToRem(100px);
    line-height: pxToRem(100px);

  }

  .mint-datetime-cancel, .confirm-btn .cancel {
    color: #656b79;
    height: pxToRem(100px);
    line-height: pxToRem(100px);
    box-sizing: border-box;
    border-right: 1px solid #EDEDED;
  }

  .picker-item {
    font-size: pxToRem(32px);
  }
</style>
